  width: 700px;
  position:absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 51;

}

.stoper {
  background:#444444 ;
  border-top: 2px solid #232323;
  border-left: 5px solid #232323;
  border-bottom: 5px solid #232323;
  border-radius: 5%;
  display: inline-block;
  height: 25px;
  width: 10px;
  z-index: 100;
}

.between_plates_trainee{
  width: 21%;

}

.plate_trainee {
  background:black;
  border-left: 5px solid black;
  border-bottom: 5px solid black;
  display: inline-block;
  z-index: 120;
  height: 90px;

}

.plate_45 {
  background: #680034;
  border-top: 2px solid #440012;
  border-left: 8px solid #440012;
  border-bottom: 8px solid #440012;
